import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';

import WebinarsData from "../../core/Data/webinars";

import AdvBox from '../../components/AdvBox/AdvBox';
import Header from '../../components/Layout/Header/Header';
import SliderCardList from '../../components/SliderCardList/SliderCardList';
import VideoCardContainer from '../../components/VideoCard/VideoCardContainer';

export default function Webinars() {
  const { t } = useTranslation("webinars");

  const [webinars, setWebinars] = useState<any>([])

  useEffect(() => {
    try {
      const res = WebinarsData;
      setWebinars(res.webinars)
    } catch (e) {
      console.log(e)
    }
  }, []);

  return (
    <>
      <Header title={t('webinars')} />

      <AdvBox title='AdvBox.card1.title' />

      <SliderCardList cardItems={webinars.filter((webinar: any) => webinar.rate > 4)} title={t('upcomingWebinars')} />

      <VideoCardContainer cardItems={webinars} title={t('RecordedWebinars')} />
    </>
  )
}
